import { ACEDriver, UserInputHelper, UserInput, DriverExtensionParameter, InstructionExtensionParameter, PlayStage, StoryAccessor } from '@alexa-games/sfb-f';
import { AudioItemUtil } from '@alexa-games/sfb-f';

// Test data and mock functions/objects

export const testUserInput: UserInput = {
    intent: "something",
    slots: [
        {
            name: "slotName",
            value: "value"
        }
    ],
    value: "raw value"
};

export function getDefaultBaseDriver(): ACEDriver {
    const driver = new ACEDriver({
        storyTitle: 'test story',
        storyID: "test-story",
        pluginName: 'default',
        scenes: [{
            id: "start",
            contents: [
                {
                    narration: "test narration"
                }
            ]
        }]
    });

    return driver;
}

export function getMockInstructionParameter(instruction : string, params : {[key: string]: string}) : InstructionExtensionParameter {
    return { instructionName: instruction,
    instructionParameters: params,
    storyState: {},
    playStage: new PlayStage(new AudioItemUtil()),
    storyAccessor: new StoryAccessor({pluginName: "", 
      storyTitle: "",
      storyID: "",
      scenes: []
    })
  };
}
